import React from 'react'

export default class AbstractArea<P = DefaultP, S = DefaultS, SS = DefaultSS> extends React.Component<P,S,SS> {
  isAA() {
    return true
  }
}

type DefaultP = ExtractDefaultP<React.Component>
type DefaultS = ExtractDefaultS<React.Component>
type DefaultSS = ExtractDefaultSS<React.Component>
type ExtractDefaultP<T> = T extends React.Component<infer P> ? P : never
// eslint-disable-next-line @typescript-eslint/no-unused-vars
type ExtractDefaultS<T> = T extends React.Component<infer P, infer S> ? S : never
// eslint-disable-next-line @typescript-eslint/no-unused-vars
type ExtractDefaultSS<T> = T extends React.Component<infer P, infer S, infer SS> ? SS : never
