import { ProSidebar,  SidebarContent,  SidebarFooter,  SidebarHeader,  Menu,  MenuItem } from 'react-pro-sidebar'
import './BBSideMenu.css'
import './BBSideMenu.module.edit.css'
import styles from './BBSideMenu.module.css'
import { Tooltip } from '@mui/material'
import AbstractArea from '../../AbstractArea'
import Area from '../../Area'
import { Icon } from '../Icon'
import { AreaWidgetProps, BaseWidgetProps, ConfigurablePropsFromDescription, getSubscription, isString, isValidIndex } from '../../../helpers/widget'
import { sideMenuConfigs } from './BBSideMenuConfigs'
import { Wrap } from '../../Wrap'
import { getFirstNonHiddenTab } from '../Tab'
import { faLock } from '@fortawesome/free-solid-svg-icons'

type Props = BaseWidgetProps & AreaWidgetProps & ConfigurableProps
type ConfigurableProps = ConfigurablePropsFromDescription<typeof sideMenuConfigs>

type State = {
  collapsed: boolean,
  selectedIndex: number,
  hideTabs: string[],
  areas: {}[],
  disableHiddenTabs: boolean
}

class BBSideMenu extends AbstractArea<Props, State> {
  state: State = {
    collapsed: false,
    selectedIndex: 0,
    hideTabs: [],
    areas: [],
    disableHiddenTabs: false
  }

  componentDidMount() {
    if (this.props.startCollapsed || this.props.onlyCollapsed)
      this.setState({ collapsed: true })

    this.updateTabs(null, this.props.hideTabs, this.props.tabSelectionSubscription)
  }

  componentDidUpdate(prevProps: Props) {
    this.updateTabs(prevProps, this.props.hideTabs, this.props.tabSelectionSubscription)
  }

  updateTabs(
    prevProps: null | Props,
    hideTabsTopic: undefined | null | string,
    tabIndexTopic: undefined | null | string,
  ) {
    const tabIndex = getSubscription(prevProps?.pubsub, this.props.pubsub, tabIndexTopic, isValidIndex)
    if (!tabIndex.isValid && tabIndex.current != null && tabIndex.topic != null)
      return alert(`Value "${tabIndex.current}" from topic "${tabIndexTopic}" is not a valid index"`)

    const hideTabs = getSubscription(prevProps?.pubsub, this.props.pubsub, hideTabsTopic, isString)
    if (!hideTabs.isValid && hideTabs.current != null && hideTabs.topic != null)
      return alert(`Value "${hideTabs.current}" from topic "${tabIndexTopic}" is not a string"`)

    if (!tabIndex.hasChanged && !hideTabs.hasChanged)
      return

    let newSelectedIndex = this.state.selectedIndex
    if (tabIndex.isValid && tabIndex.hasChanged) newSelectedIndex = tabIndex.current

    let newHideTabs: string[] = []
    if (hideTabs.isValid && hideTabs.hasChanged) {
     
      newHideTabs = hideTabs.current.split(',')
      const disableHiddenTabs=this.props.disableHiddenTabs??false;
      this.setState({ hideTabs: newHideTabs,disableHiddenTabs:disableHiddenTabs })
    }

    if (this.props.tabs?.length && newSelectedIndex != null) {
      const tabHideId = this.props.tabs[newSelectedIndex]
      const firstNonHiddenIndex = getFirstNonHiddenTab(this.props.tabs, this.props.tabs, newHideTabs)
      if (firstNonHiddenIndex != null && newHideTabs?.includes(tabHideId))
        newSelectedIndex = firstNonHiddenIndex
    }

    if (newSelectedIndex !== this.state.selectedIndex) this.setState({ selectedIndex: newSelectedIndex })
  }

  onTabClick = (index: number, event: React.MouseEvent<HTMLLIElement, MouseEvent>) => {
    if (event) event.stopPropagation()

    let tabSelectionSubscription = this.props.tabSelectionSubscription
    if (tabSelectionSubscription) {
      this.props.publish(tabSelectionSubscription, index)
    } else {
      this.setState({ selectedIndex: index })
    }
  }

  headerClicked = () => {
    if (this.props.onlyCollapsed) return

    var collapsed = !this.state.collapsed
    this.setState({ collapsed: collapsed })
  }

  render() {
    var loadPages=true;
    if (this.props.lazyLoadingPages===true) loadPages=false;

    try {
      let backgroundimage = this.props.backgroundimage
      let footertext = this.props.footertext
      let headertext = this.props.headertext
      let state = this.state
      let collapsed = this.state.collapsed
      let className = this.props.className

      let onlyCollapsed = this.props.onlyCollapsed
      if (onlyCollapsed==null) onlyCollapsed=false;

      return (
        <div  className="app sideMenu "
              data-custom-styling={Boolean(this.props.customStyling)}
              style={{
                display: 'flex',
                height: '100%',
                direction: this.props.rightside ? 'rtl' :'ltr',
                '--sidemenu-background-color': this.props.sidemenuBackgroundColor,
                '--sidemenu-tab-color': this.props.sidemenuTabColor,
                '--sidemenu-tab-icon-color': this.props.sidemenuTabIconColor,
                '--sidemenu-tab-text-color': this.props.sidemenuTabTextColor,
                '--sidemenu-tab-hover-color': this.props.sidemenuTabHoverColor,
                '--sidemenu-tab-icon-hover-color': this.props.sidemenuTabIconHoverColor,
                '--sidemenu-tab-text-hover-color': this.props.sidemenuTabTextHoverColor
              } as any}
         >

          <ProSidebar
            collapsedWidth="46px"
            rtl={Boolean(this.props.rightside)}
            collapsed={state.collapsed}
            {...(backgroundimage && { image: backgroundimage })}
          >
            {!onlyCollapsed && (
            <SidebarHeader>
              {collapsed ? (
                <Icon
                  preserveAspectRatio="xMaxYMin meet"
                  onClick={this.headerClicked}
                  icon={this.props.rightside ? "angle-left" : "angle-right"}
                  size="1x"
                />
              ) : (
                <Icon
                  onClick={this.headerClicked}
                  icon={this.props.rightside ? "angle-right" : "angle-left"}
                  size="1x"
                />
              )}

              {!collapsed && headertext && (
                <center>
                  <h2>{headertext}</h2>
                </center>
              )}
            </SidebarHeader>
            )}

            <SidebarContent>
              <Menu className={className ?? undefined} iconShape="round" popperArrow>

                { 
                
                (this.props.tabs ?? []).map((tab, index) => {
                   // console.log('processing tab', tab) 

                  let selected = index === this.state.selectedIndex

                  if (this.state.hideTabs.length && this.state.hideTabs.includes(tab))
                  if (!this.state.disableHiddenTabs)
                  {
                    return null
                  }
                  else
                  {
                    return (
                      <Wrap when={collapsed} key={tab} with={(children) => (
                        <Tooltip title={tab} disableInteractive>{children as any}</Tooltip>
                      )}>
                        <MenuItem 
                          className={ undefined }
                      
                          icon={<Icon  color={'DarkGray'} opacity={0.99} icon={faLock} /> }
                        >
                          {tab}
                        </MenuItem>
                      </Wrap>
                  )
                }
                   

                
                  return (
                    <Wrap when={collapsed} key={tab} with={(children) => (
                      <Tooltip title={tab} disableInteractive>{children as any}</Tooltip>
                    )}>
                      <MenuItem
                        className={selected ? 'active' : undefined}
                        onClick={(event) => this.onTabClick(index, event)}
                        icon={<Icon icon={this.props.icons?.[index] as any ?? 'beer'} /> }
                      >
                        {tab}
                      </MenuItem>
                    </Wrap>
                )})}
              </Menu>
            </SidebarContent>
            <SidebarFooter>
              {footertext && (
                <center>
                  <h4>{footertext}</h4>
                </center>
              )}
            </SidebarFooter>
          </ProSidebar>

          <div className="main">
            {(this.props.tabs ?? []).map((tab, index) => {
              let areaConfig = {}
              try {
                areaConfig = this.props.areas[index] || {}
              } catch (e) {}

              let selected = index === this.state.selectedIndex
              var extra = ' main '
              if (!selected && !loadPages) return null

              return (
                <div
                  key={index + 'div'}
                  className={styles.areaContainer + extra}
                  style={{ display: selected ? 'flex' : 'none' }}
                >
                  <Area
                    {...this.props}
                    key={index + 'Area'}
                    index={index}
                    config={areaConfig}
                    onChange={this.props.onChange}
                    withinSelectedTab={selected}
                  />
                </div>
              )
            })}
          </div>
        </div>
      )
    } catch (error) {
      console.log(error)
      return null
    }
  }
}

export default BBSideMenu
