import { Fragment, ReactElement } from 'react'

type Props = FragmentProps & {
  when: boolean,
  with: (children: FragmentProps['children']) => ReactElement,
}
type FragmentProps = Parameters<typeof Fragment>[0]

export function Wrap(props: Props) {
  if (!props.when) return <Fragment>{props.children}</Fragment>
  return props.with(props.children)
}
